.slider-container {
  position: relative;
  width: 100%;
  height: 400px;
  overflow: hidden;
}

.slider {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 300%;
  height: 100%;
  transition: transform 0.5s ease-in-out;
}

.slider-item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  height: 100%;
  width: 33.33%;
  background-color: #f2f2f2;
  transition: transform 0.5s ease-in-out;
}

.slider-item.active {
  transform: translateX(0%);
}

.slider-item.next {
  transform: translateX(100%);
}

.slider-item.prev {
  transform: translateX(-100%);
}

.slider-item-content {
  text-align: center;
  padding: 2rem;
}

.slider-item-content h3 {
  margin-bottom: 1rem;
  font-size: 2rem;
}

.slider-item-content p {
  font-size: 1.2rem;
}

.slider-controls {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.slider-control {
  cursor: pointer;
  font-size: 2rem;
  color: #333;
  margin: 0 1rem;
  transition: color 0.3s ease-in-out;
}

.slider-control:hover {
  color: #666;
}
